import React, {Component} from "react";
import {connect} from "react-redux";
import PropTypes from "prop-types";
import {getPricesForPeriodSelectorForBundles} from "../selectors";
import {getConsentsWithBonuses} from 'eshop/modules/checkout/selectors';
import _ from 'lodash';
import {Section, Trigger} from "eshop/modules/core/components/ui/Expander";
import {maleOrdinal} from "./Utils";

class SummaryDetailsTableForBundles extends Component {
    render() {
        let title = this.computeTitle(this.props.entry.monthFrom, this.props.entry.monthTo);
        return (
            <Section header={this.getHeaderView(title, this.props.bundleNo)} variant="section_expander"
                    headerClassName="opl-section__header u-no-margin u-no-padding-left"
                summaryClassName="opl-section__border-bottom" className="opl-section--chevron-left u-no-margin-top u-large-no-padding-top u-medium-no-padding-top u-padding-left-l-xl u-small-padding-top-l "
                contentClassName="u-small-padding-top-l">

                    <table key={"details-table-bundle" + this.props.bundleNo} className="u-no-margin-t opl-table opl-conf-summary-table opl-table--50-50 u-no-border">
                        <caption className="u-acc-hide">{title}</caption>
                        <thead>
                            <tr className="u-acc-hide">
                                <th></th>
                                <th></th>
                                <th className="second-last u-font-normal">{this.props.oneTimeHeader}</th>
                                <th className="last u-font-normal">{this.props.monthlyHeader}</th>
                            </tr>
                        </thead>
                        <tbody>
                            {this.props.prices.map((price, bundleNo) =>
                                this.prepareTableRow(price, bundleNo)
                                )

                            }
                        </tbody>
                    </table>

            </Section>
        );
    }

    getHeaderView(title, bundleNo) {
        return (
        [
            this.getSummaryView(title, bundleNo),
            <Trigger key={"Nested-trigger-bundle-" + bundleNo } className="g-brand2-c">
                <span className="opl-section__text-show">rozwiń</span>
                <span className="opl-section__text-hide">zwiń</span>
            </Trigger>,
            <div key={"Nested-bundle-" + bundleNo} id={this.props.id}></div>
        ]
        );
    }

    getSummaryView(title, bundleNo) {
        const textClass = "u-font-bold g-brand2-c u-no-margin";

        let monthlyPrice = (this.props.entry.monthlyPrice) ?
            <span className={textClass}>{this.formatPrice(this.props.entry.monthlyPrice, this.props.entry.currency)}</span> : this.renderNoPrice("g-brand2-c u-no-margin");

        let oneTimePrice = (this.props.entry.oneTimePrice) ?
            <span className={textClass}>{this.props.entry.oneTimePrice}</span> : this.renderNoPrice("g-brand2-c u-no-margin");

        return (
          <table id={"SummaryView-bundleNo-"+bundleNo} key={"table-bundle-" + bundleNo } className="opl-table opl-conf-summary-table opl-table--50-50 u-no-margin-t no-padding-top">
          <thead>
            <tr className="u-acc-hide">
              <th></th>
              <th></th>
              <th className="second-last u-font-normal u-small-hide">{this.props.oneTimeHeader}</th>
              <th className="last u-font-normal u-small-hide">{this.props.monthlyHeader}</th>
            </tr>
          </thead>
            <tbody>
              <tr className="u-small-no-padding">
                 <th  className="u-w-auto u-no-border u-padding-left-l-xl u-no-padding-top u-padding-l" >
                  <span className="u-margin-right u-inline-block u-font-bold g-brand2-c">{title}</span>
                </th>
                <td data-title={this.props.oneTimeHeader} className="second-last g-brand2-c u-no-border u-no-padding-top u-padding-l u-padding-right-l">{oneTimePrice}</td>
                <td data-title={this.props.monthlyHeader} className="last g-brand2-c u-no-border u-no-padding-top u-padding-l">{monthlyPrice}</td>
              </tr>
            </tbody>
          </table>
        );
    }

    isInPeriod(monthFrom, monthTo) {
        return monthFrom <= this.props.entry.monthFrom && (monthTo === null || (this.props.entry.monthTo !== null && monthTo >= this.props.entry.monthTo));
    }

    formatPrice(monthlyPrice, currency) {
        let finalPrice = monthlyPrice;
        return finalPrice.toFixed(2).replace(".", ",") + " " + currency;
    }

    prepareTableRow(price, bundleNo) {
        const textClass = "u-font-bold u-no-margin";

        if (!price.monthlyPrice && !price.oneTimeCost){
            return;
        }

        let monthlyPrice = (price.monthlyPrice) ?
            <span className={textClass}>{price.monthlyPrice}</span> : this.renderNoPrice();

        let oneTimePrice = (price.oneTimeCost) ?
            <span className={textClass}>{price.oneTimeCost.replace(".", ",")}</span> : this.renderNoPrice();
        return (
            <tr key={"price" + bundleNo} className="u-border-bottom u-no-border-t">
                <th className="u-no-border u-small-no-padding u-w-auto u-font-normal u-medium-padding-left-l-xl u-large-padding-left-l-xl u-vertical-middle">{price.description}</th>
                <td data-title={this.props.oneTimeHeader}
                    className="second-last u-no-border u-font-normal u-small-padding-top-l u-padding-right-l u-vertical-middle">{oneTimePrice}</td>
                <td data-title={this.props.monthlyHeader}
                    className="last u-no-border u-font-normal u-small-padding-top-l u-vertical-middle">{monthlyPrice}</td>
            </tr>
        );
    }

    prepareBonusRow(bonus, bundleNo) {
        let price = bonus.priceSet ? bonus.priceSet : bonus.priceModification;
        let monthlyPrice = <span className="h5 u-no-margin u-font-normal">
            {"-" + price.toFixed(2).replace(".", ",") + " " + this.props.entry.currency}
        </span>;
        let oneTimePrice = <span aria-label="brak">&mdash;</span>;

        return (
            <tr key={"bonus" + bundleNo} className="u-border-bottom u-small-padding u-small-no-margin u-no-border-t">
                <th className="u-no-border u-small-padding-top u-small-padding u-w-auto u-no-padding-left">{bonus.title}</th>
                <td data-title={this.props.oneTimeHeader}
                    className="second-last u-no-border u-font-normal u-small-padding-top">{oneTimePrice}</td>
                <td data-title={this.props.monthlyHeader}
                    className="second-last u-no-border u-font-normal u-small-padding-top">{monthlyPrice}</td>
            </tr>
        );
    }

    componentDidMount() {
        if ((!this.props.entry.monthFrom && !this.props.entry.monthTo) || (this.props.entry.monthFrom === 1 && this.props.entry.monthTo === 1)) {
            OPL.UI.loadModules(document.getElementById(this.props.id), [{
                path: 'core/modules/tooltips',
                options: {"mouseoverMinWidth":0, "triggerEvent": "mouseover"}
            }]);
        }
    }

    computeTitle(from, to) {
        if (!from && !to) {
            return this.props.checkoutPriceText;
        } else if ((from === 1 && to === 1) || (!from && to===1)) {
            return this.props.firstMonthPriceText || "W "+maleOrdinal(1)+" miesiącu";
        } else if (from === to) {
            return "W "+maleOrdinal(from)+" miesiącu";
        } else {
            return "Od " + from + ((to) ? " do " + to : "") + " miesiąca";
        }
    }

    computeTooltip(from, to) {
        if (!from && !to) {
            return this.props.checkoutPriceTooltipContent;
        } else if (from === 1 && to === 1) {
            return this.props.firstMonthPriceTooltipContent;
        }
    }
    renderNoPrice (className=undefined){
        return (
            <div className='u-inline'>
                <span className={className} aria-hidden="true">&mdash;</span>
                <span className="u-acc-hide">brak</span>
            </div>
        )
    }
}


SummaryDetailsTableForBundles.propTypes = {
    monthlyHeader: PropTypes.string.isRequired,
    oneTimeHeader: PropTypes.string.isRequired,
    checkoutPriceText: PropTypes.string,
    firstMonthPriceText: PropTypes.string,
    id: PropTypes.string,
    checkoutPriceTooltipContent: PropTypes.string,
    firstMonthPriceTooltipContent: PropTypes.string
};

SummaryDetailsTableForBundles.defaultProps = {
    checkoutPriceText: 'Opłata na start',
    firstMonthPriceText: "W pierwszym miesiącu",
    highlighted: false
};

const mapStateToProps = () => {
    const getPricesForPeriod = getPricesForPeriodSelectorForBundles();

    return (state, props) => ({
        prices: getPricesForPeriod(state, props),
        bonusConsents: getConsentsWithBonuses(state)
    });
};

export default connect(mapStateToProps)(SummaryDetailsTableForBundles);